@import "./public-flex.scss";
body, html {
  height: 100%;
  width: 100%;
  position: relative;
}

.header {
  width: 100%;
  height: 70px;
  background-color: rgba(255, 255, 255, .3);
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.8);
  position: absolute;
  z-index: 10;


  @include flex-jc-between;

  &-logo {
    width: 120px;
    height: 100%;
    z-index: 10;
    padding: 0 15px;

    img {
      width: 60px;
      border: 5px solid #880;
      border-radius: 50%;
    }
  }

  &-list {
    @include flex-c;
  }

  &-option {
    width: 100px;
    height: 100%;
    @include flex-c;
    cursor: pointer;

    &-txt {
      display: block;
      position: relative;
      color: #fff;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: -5px;
        width: 100%;
        height: 1px;
        background-color: #000;
        transform: scalex(0);
        transition: all .3s ease;
      }
      
      &:hover {
        &::before {
          transform: scalex(1);
        }
      }
    }
  }
}

.body {
  width: 100%;
  height: 100%;
  position: relative;
  @include flex-jc-c;

  &-title {
    color: #fff;
    position: absolute;
    font-size: 50px;
    top: 255px;
  }

  img {
    width: 100%;
  }
}